import * as THREE from 'three'
import { useRef, useState, useEffect } from 'react'
import { Canvas, useFrame, useThree } from '@react-three/fiber'
import { Image, ScrollControls, Scroll, useScroll, Text, Box } from '@react-three/drei'
import { proxy, useSnapshot } from 'valtio'
import { easing } from 'maath'
import components from './ItemComponents'

const material = new THREE.LineBasicMaterial({ color: 'white' })
const geometry = new THREE.BufferGeometry().setFromPoints([new THREE.Vector3(0, -0.5, 0), new THREE.Vector3(0, 0.5, 0)])
const state = proxy({
  clicked: 0,
  urls: ['thum1', 'thum2', 'thum3', 'thum4', 'thum5', 'thum7', 'thum8', 'thum9', 'thum10'].map((u) => `./${u}.jpg`),
  isChanged: [true, true, true, false, false, false, false, false, false],
  changeUrls: ['main1', 'main2', 'main3', 'main4', 'main5', 'main7', 'main8', 'main9', 'main10'].map((u) => `./${u}.jpg`),
  msg: ['', '', '', '', '', '', '', '', '']
})

function Minimap() {
  const ref = useRef()
  const scroll = useScroll()
  const { urls } = useSnapshot(state)
  const { height } = useThree((state) => state.viewport)
  useFrame((state, delta) => {
    ref.current.children.forEach((child, index) => {
      // Give me a value between 0 and 1
      //   starting at the position of my item
      //   ranging across 4 / total length
      //   make it a sine, so the value goes from 0 to 1 to 0.
      const y = scroll.curve(index / urls.length - 1.5 / urls.length, 4 / urls.length)
      easing.damp(child.scale, 'y', 0.15 + y / 6, 0.15, delta)
    })
  })
  return (
    <group ref={ref}>
      {urls.map((_, i) => (
        <line key={i} geometry={geometry} material={material} position={[i * 0.06 - urls.length * 0.03, -height / 2 + 0.4, 0]} />
      ))}
    </group>
  )
}

function Item({ index, position, scale, c = new THREE.Color(), component: Component, ...props }) {
  const ref = useRef()
  const textRef = useRef()
  const groupRef = useRef()
  const scroll = useScroll()
  const { width, height } = useThree((state) => state.viewport)
  const { clicked, urls, isChanged, changeUrls, msg } = useSnapshot(state)
  const [hovered, hover] = useState(false)
  const [opacity, setOpacity] = useState(1)
  const extraTop = height * 0.2

  useEffect(() => {
    setOpacity(0)
    const timeout = setTimeout(() => setOpacity(1), 300)
    return () => clearTimeout(timeout)
  }, [isChanged[index] && clicked === index ? changeUrls[index] : urls[index]])

  const click = () => {
    if (state.clicked === index) {
      state.clicked = null
    } else {
      state.clicked = index
    }

    // state.clicked = index === clicked ? null : index
    if (state.clicked !== null) {
      const itemSpacing = height * 0.15
      const totalHeight = urls.length * itemSpacing + extraTop * 2
      const targetY = (index + 0.5) * itemSpacing + extraTop - height / 2
      const scrollOffset = targetY / (totalHeight - height)
      scroll.el.scrollTo({
        top: scrollOffset * (scroll.el.scrollHeight - scroll.el.clientHeight),
        behavior: 'smooth'
      })
    }
  }
  const over = () => hover(true)
  const out = () => hover(false)
  const move = () => {
    state.clicked = null
  }
  useFrame((state, delta) => {
    const y = scroll.curve(index / urls.length - 1.5 / urls.length, 4 / urls.length)
    const baseWidth = scale[0]
    const baseHeight = scale[1]
    if (clicked !== null) {
      // 클릭된 아이템만 확대 및 칼라, 나머지는 기본 사이즈/그레이
      const isClicked = clicked === index
      easing.damp3(ref.current.scale, [isClicked ? baseWidth * 1.4 : baseWidth, isClicked ? baseHeight * 3.9 : baseHeight, 1], 0.15, delta)
      ref.current.material.scale[0] = ref.current.scale.x
      ref.current.material.scale[1] = ref.current.scale.y
      // 그레이스케일/칼라 처리
      easing.damp(ref.current.material, 'grayscale', isClicked ? 0 : 1, 0.15, delta)
      easing.dampC(ref.current.material.color, isClicked ? 'white' : '#aaa', 0.15, delta)
      // 텍스트 투명도
      if (textRef.current) {
        easing.damp(textRef.current.material, 'opacity', isClicked ? 0 : 1, 0.15, delta)
      }
    } else {
      // 스크롤 기반 애니메이션 (기존과 동일)
      easing.damp3(ref.current.scale, [baseWidth + y * 0.7, baseHeight, 1], 0.15, delta)
      ref.current.material.scale[0] = ref.current.scale.x
      ref.current.material.scale[1] = ref.current.scale.y
      if (textRef.current) {
        easing.damp(textRef.current.material, 'opacity', 1, 0.15, delta)
      }
      easing.damp(ref.current.material, 'grayscale', Math.max(0, 1 - y * 5), 0.15, delta)
      easing.dampC(ref.current.material.color, 'white', 0.15, delta)
    }
    // 위치 이동(중앙 이동 등)은 기존과 동일하게 유지
    const spacing = height * 0.2
    if (clicked !== null && index < clicked) easing.damp(groupRef.current.position, 'y', position[1] + spacing, 0.15, delta)
    if (clicked !== null && index > clicked) easing.damp(groupRef.current.position, 'y', position[1] - spacing, 0.15, delta)
    if (clicked === null || clicked === index) easing.damp(groupRef.current.position, 'y', position[1], 0.15, delta)
  })
  return (
    <group ref={groupRef} position={position}>
      <Image
        ref={ref}
        {...props}
        position={[0, 0, 0]}
        scale={scale}
        onClick={click}
        onPointerOver={over}
        onPointerOut={out}
        onPointerMove={move}
        url={clicked === index ? changeUrls[index] : urls[index]}
        style={{ opacity, transition: 'opacity 0.3s ease-in-out' }}
      />
      {clicked === index ? <Component isVisible={clicked === index} scale={scale} clicked={clicked} /> : null}
      <Text
        ref={textRef}
        position={[0, 0, 0.01]}
        fontSize={0.2}
        color="white"
        anchorX="center"
        anchorY="middle"
        font="./fonts/kotra_songelssi.ttf">
        {msg[index]}
      </Text>
    </group>
  )
}

function Items({ gap = 0.15 }) {
  const { urls } = useSnapshot(state)
  const { width, height } = useThree((state) => state.viewport)
  const itemWidth = width * 0.7
  const itemHeight = height * 0.14
  const itemSpacing = itemHeight * 1.15 // 아이템 간 간격을 viewport 높이의 15%로 설정
  const extraTop = itemSpacing * 2 // 0번 위에만 2배 추가 여백
  return (
    <ScrollControls damping={0.1} pages={(urls.length * itemSpacing + extraTop * 2) / height}>
      <Minimap />
      <Scroll>
        {urls.map((url, i) => (
          <Item
            key={i}
            index={i}
            position={[0, height / 2 - (i + 0.5) * itemSpacing - extraTop, 0]}
            scale={[itemWidth, itemHeight, 1]}
            url={url}
            component={components[i]}
          />
        ))}
      </Scroll>
    </ScrollControls>
  )
}

export const App = () => (
  <Canvas
    gl={{ antialias: false }}
    dpr={[1, 1.5]}
    //onPointerMissed={() => (state.clicked = null)}
    //onPointerMove={() => (state.clicked = null)}
  >
    <Items />
  </Canvas>
)
