const HandwritingText = ({ text, isVisible, font, fontSize, position, anchorY, delay }) => {
  const [displayedText, setDisplayedText] = useState('')
  const [isLoaded, setIsLoaded] = useState(false)
  const indexRef = useRef(0)

  const opacityRef = useRef(isVisible ? 1 : 0)
  const textRef = useRef()

  useFrame((state, delta) => {
    const targetOpacity = isVisible ? 1 : 0
    easing.damp(opacityRef, 'current', targetOpacity, 0.5, delta)
    if (textRef.current) {
      textRef.current.material.opacity = opacityRef.current
    }

    if (isVisible == 0) {
      setDisplayedText('')
    }
  })

  useEffect(() => {
    const loadResources = async () => {
      await new Promise((resolve) => setTimeout(resolve, 1000))
      setIsLoaded(true)
    }

    loadResources()
  }, [])

  useEffect(() => {
    if (isVisible && isLoaded) {
      const startAnimation = () => {
        setDisplayedText(text[0])
        indexRef.current = 0
        const interval = setInterval(() => {
          if (indexRef.current < text.length - 1) {
            setDisplayedText((prev) => prev + text[indexRef.current])
            indexRef.current += 1
          } else {
            clearInterval(interval)
          }
        }, 100)
      }

      const delayTimeout = setTimeout(startAnimation, delay)

      return () => {
        clearInterval(delayTimeout)
      }
    }
  }, [isVisible, isLoaded, text, delay])

  return (
    <group>
      <Text ref={textRef} font={font} fontSize={fontSize} color="black" anchorX="center" anchorY={anchorY} position={position}>
        {displayedText}
      </Text>
    </group>
  )
}

